.container {
    margin-top: 70px;
    width: 230px;
    display: grid;
    grid-template-rows: 1fr 1fr;
    gap: 40px;
}

.wrapper {
    display: grid;
    grid-template-columns: 11px 1fr 28px;
    gap: 20px;
    align-items: center;
}

.number {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
}
